<template>
  <div class="nes-container is-dark_cus">
    <h2>게시글</h2>
    <form>
      <div class="form-group">
        <label for="title">제목</label>
        <input type="text"  class="nes-input is-dark" id="title" v-model="post.title" placeholder="제목을 입력하세요">
      </div>
      <div class="form-group">
        <label for="content">내용</label>
        <textarea id="textarea_field" class="nes-textarea is-dark" maxlength="100" v-model="post.contents"  placeholder="내용을 입력하세요"></textarea>
      </div>
      <div class="form-group email-group">
        <label for="email">이메일</label>
        <div class="email-input-container">
          <input type="text" class="nes-input is-dark" id="email" v-model="post.email" placeholder="이메일을 입력하세요">
        </div>
      </div>
      <div class="form-group">
        <label for="files">파일</label>
        <div class="nes-input is-dark">
          <input type="file" multiple class="form-controls file-input" id="files" @change="onFileChange">
        </div>
      </div>
      <button class="nes-btn is-primary" @click="goToList">목록으로</button>
      <button type="submit" class="nes-btn is-primary" @click.prevent="submitPost">수정 완료</button>
    </form>
  </div>
</template>

<style>
.form-group {
  margin-bottom: 20px;
}
.email-input-container{
  display: flex;
}

.file-input-container {
  width: 300px;
}

label {
  font-weight: bold;
}
textarea {
  height: auto;
  min-height: 300px;
  resize: none;
}
.email-input {
  background: black;
  display: flex;
  margin-bottom: 20px;
  width: 200px;
}
.email-domain-select {
  width: 150px;
  font-size: 14px;
  height: 33px;
  margin-left: 10px;
}
.form-controls {
  background: black;
  border-radius: 0;
  border: none;
  border-bottom: 1px solid #eee;
  box-shadow: none;
  width: 100%;
}
.form-controls:focus {

  border-color: #007bff;
  box-shadow: none;
}
.btn {
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 0;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: bold;
  transition: background-color 0.3s ease-in-out;
}
.btn:hover {
  background-color: #0069d9;
  color: #fff;
}
</style>

<script>
import axios from "axios";
import router from "../../router";

export default {
  name: "PostModify",
  data() {
    return {
      post:  [
        {
          id:"",
          title: "",
          contents: "",
          email: "",
          username:"",
          createdDate:"",
          file: []
        }
      ]
    };
  },

  created() {
    const postId = this.$route.params.id;
    this.axios = axios.create({
      baseURL: this.$myUrl
    });
    try {
      this.axios
        .get(`/api/Detail/${postId}`)
        .then(response => {
          this.post = response.data;
        })
        .catch(error => {

        });
    } catch (error) {
    }
  },

  methods: {
    limitText(event) {
      const maxLength = 100;
      if (event.target.value.length > maxLength) {
        event.target.value = event.target.value.substring(0, maxLength);
      }
    },

    goToList() {
      this.$router.push('/PostList');
    },
    submitPost() {
      const postId = this.$route.params.id;
      const data = {
        id:this.$route.params.id,
        title: this.post.title,
        contents: this.post.contents,
        email: this.post.email,
        username:this.post.username,
        createdDate: this.post.createdDate,
        file :[],
      };
      const config = {
        headers: {
          'Content-Type': 'application/json'
        }
      };
      try {
        this.axios.patch(`/api/Notice/Patch/${postId}`, data,config)
          .then(response => {
            this.$alert('게시판', '수정 성공');
            this.$router.push('/PostList');
          })
          .catch(error => {
            this.$alert('게시판', '수정 실패');
          });
      } catch (error) {
        this.$alert('게시판', '수정 오류');
      }
    },
    onFileChange(){

    }
  },

};
</script>



