<template>
  <div class="nes-container is-dark_cus">
  <div class="nes-table-responsive"><!--div class="post-list"-->

    <table class="nes-table is-bordered is-dark">
      <thead>
      <tr class="hover-style">
        <th class="list-header list-header-id">순번</th>
        <th class="list-header list-header-title">제목</th>
        <th class="list-header list-header-userid">아이디</th>
        <th class="list-header list-header-email">이메일</th>
        <th class="list-header list-header-date">등록날짜</th>
      </tr>
      </thead>
      <tbody v-if="posts.length">
      <tr v-for="(post, index) in posts" :key="index" @mouseover="setActivePost(index)" @click="goToPostDetail">
        <td>{{ post.id }}</td>
        <td>{{truncateText( post.title,10 )}}</td>
        <td>{{ truncateText(post.username,10 ) }}</td>
        <td>{{truncateText( post.email,10 ) }}</td>
        <td>{{ post.createdDate }}</td>
      </tr>
      </tbody>
      <tbody v-else>
      <tr>
        <td colspan="5" class="no-data">데이터가 없습니다.</td>
      </tr>
      </tbody>
    </table>
    <div class="post-create">
      <button class="nes-btn is-primary" @click="goToCreatePost">글쓰기</button>
      <button class="nes-btn is-primary" @click="goToMain">홈으로</button>
    </div>
  </div>
  </div>
</template>

<style>
.post-list {
  max-width: 800px;
  margin: 20px auto;
}
.post-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.post-title {
  font-size: 24px;
  font-weight: bold;
  margin-left: 20px;
}

.post-filter {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  float: right;
  margin-right: 140px;
}
tr:hover {
  background-color: blue;
}
.hover-style:not(:hover){
  background-color: blue;
}

.post-filter input {
  margin-right: 5px;
  float: right;
}
table {
  border-collapse: collapse;
  margin: 20px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 14px;
  width: 80%;
}

th.list-header {
  border-bottom: 1px solid #ddd;
  padding: 8px;

}
.list-header-id {
  width: 50px;
  text-align: center;
}

.list-header-title {
  width: 300px;
  text-align: center;
}

.list-header-userid {
  width: 80px;
  text-align: center;
}

.list-header-email {
  width: 150px;
  text-align: center;
}

.list-header-date {
  width: 130px;
  text-align: center;
}
td {
  border-bottom: 1px solid #ddd;
  padding: 8px;
}

td:last-child {
  text-align: right;
}
td[colspan="5"].no-data {
  text-align: center;
  font-style: italic;
  border-bottom: 1px solid #ccc;
}
.post-create {
  margin-bottom: 20px;
  margin-left: 20px;
}
</style>

<script>
import axios from "axios";

export default {
  name: "PostList",
  data() {
    return {
      posts:[],
    };
  },
  created() {
    this.axios = axios.create({
      baseURL: this.$myUrl
    });
    try {
      this.axios
        .get('/api/Notice')
        .then(response => {
          this.posts = response.data;
        })
        .catch(error => {
        });
    } catch (error) {
    }
  },
  methods: {
    truncateText(text, maxLength) {
      if (!text) return '';
      if (text.length <= maxLength) return text;
      return text.substring(0, maxLength) + '...';
    },
    goToCreatePost() {
      this.$router.push('/PostForm');
    },
    goToMain() {
      this.$router.push('/Main');
    },
    setActivePost(index) {
      this.activePostIndex = index;
    },
    goToPostDetail() {
      if (this.activePostIndex !== null) {
        const post = this.posts[this.activePostIndex];

                this.$router.push({
                  name: "PostDetail",
                  params: {
                    id: post.id,
                  },
                });
      }
    },
  }
};
</script>



