<template>
  <div :style="cssProduct">
    <img :src="imageUrlEdit" :style="cssImage"/>
    <div>
      <video  :src="pVideo" :style="cssImage"  width="640" height="360" controls></video>
    </div>
    <div :style="cssTitle">
        <div input v-if="pAuthority ==='admin'" >
          상품 시리얼 넘버 : {{this.pProductNumber}}
        </div>
        <div input v-if="pAuthority ==='admin'">

          <input v-if="pAuthority ==='admin'" ref="imageUpload" type="file" accept="image/*" @change="onImageFileChange"  />
          {{this.imageUrlEdit}}
        </div>
        <div>
          <input  v-if="pAuthority ==='admin'" :value="titleEdit" @input="onEditTitle"/>
          {{this.titleEdit}}
        </div>
         <div >
          <input v-if="pAuthority ==='admin'" :value="priceEdit" @input="onEditPrice"/>
          {{this.priceEdit}}원
        </div>
        <div>
          갯수 :
          <button v-on:click="decreaseCount">-</button>
            {{this.pCount}}
          <button v-on:click="incrementCount">+</button>
        </div>

        <div class="buyButton">
          <br>
          <button class="btn btn-primary"  v-on:click="" :style="cssButton">장바구니</button>
          <button class="btn btn-primary"  v-on:click="" :style="cssButton">구매</button>
        </div>
        <div>
          <br>
          <button class="btn btn-primary" v-on:click="incrementReport">신고하기</button>
          <button  v-if="pAuthority ==='admin'"  class="btn btn-primary" v-on:click="saveData" >데이터 저장</button>
        </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "ProductComponent",
  props:{
    pAuthority:{
      type:String,
      default:'custom'
    },
    pProductNumber:{
      type:String,
      default:'0'
    },
    pWidth:{
      type:Number,
      default:500
    },
    pHeight:{
      type:Number,
      default:600
    },
    pScore:{
      type:Number,
      default:1
    },
    pCount:{
      type:Number,
      default:1
    },
    pReport:{
      type:Number,
      default:1
    },
    pbtWidth:{
      type:Number,
      default:250
    },
    pbtHeight:{
      type:Number,
      default:55
    },
    pMaxImageHeight:{
      type:Number,
      default:400
    },
    pMaxImageWidth:{
      type:Number,
      default:400
    },
   pMaxBtHeight:{
    type:Number,
    default:40
   },
   pMaxBtWidth:{
    type:Number,
    default:150
   },
    pMinImageHeight:{
      type:Number,
      default:250
    },
    pMinImageWidth:{
      type:Number,
      default:250
    },
    pMinBtHeight:{
      type:Number,
      default:25
    },
    pMinBtWidth:{
      type:Number,
      default:80
    },
    pVideo: {
      type: String,
      default: 'https://cdn.akamai.steamstatic.com/steam/apps/256938319/movie480_vp9.webm?t=1680071383'
    },
  },
  data() {
    return {
      screenWidth    : window.innerWidth,
      screenHeight   : window.innerHeight,
      screenMaxWidth : window.screen.width,
      screenMaxHeight: window.screen.height,
      imageUrlEdit   : 'https://www.shutterstock.com/blog/wp-content/uploads/sites/5/2019/09/shutterstock_1429964489.jpg?w=2000',
      priceEdit      : '0',
      titleEdit      : '제목',
    }
  },
  computed:{
    cssImage(){
      return{
        float     :'left',
        width     :((this.pWidth)*(this.screenWidth/this.screenMaxWidth))+'px',
        height    :((this.pHeight)*(this.screenHeight/this.screenMaxHeight))+'px',
        maxWidth  :this.pMaxImageWidth+'px',
        maxHeight :this.pMaxImageHeight+'px',
        minWidth  :this.pMinImageWidth+'px',
        minHeight :this.pMinImageHeight+'px',
      }
    },
    cssScore(){
      return{
        float:'left',
      }
    },
    cssReport(){
      return{
        float:'left',
      }
    },
    cssResize(){
      return{
        width:this.screenWidth,
        height:this.screenHeight,
      }
    },
    cssTitle(){
      return{
         float:'left',
      }
    },
    cssButton(){
      return{
        width     : ((this.pbtWidth)*(this.screenWidth/this.screenMaxWidth))+'px',
        height    : ((this.pbtHeight)*(this.screenHeight/this.screenMaxHeight))+'px',
        maxWidth  : this.pMaxBtWidth+'px',
        maxHeight : this.pMaxBtHeight+'px',
        minWidth  : this.pMinBtWidth+'px',
        minHeight : this.pMinBtHeight+'px'
      }
    },
    cssProduct(){
      return{
        position: 'relative',
        width: '1500px',
        height: '100%',
        margin: '0 auto',
        padding: '0 30px',
        display:'flex' ,
        flexDirection:'row' ,
      }
    },
  },
  methods:{
    incrementCount(){
      this.pCount++;
    },
    decreaseCount(){
      if(this.pCount>1){
        this.pCount--;
      }
    },
    incrementReport(){
      this.pReport++;
    },
    saveData(){
      /*
      *  제목
      *  --키값 db에서 처리 상풍번호
      *  가격
      *  이미지 경로
      * */
    },
    buyProduct(){
      /*
      *  사용자 정보
      *  구매 가격
      *  상품 구매 갯수
      *  제목
      *  상품번호
      * */
    },
    gatherProduct(){

    },
    handleResize(event) {
      this.screenWidth = window.innerWidth;
      this.screenHeight = window.innerHeight;
    },
    onEditTitle(e) {
      this.titleEdit = e.target.value
    },
    onEditPrice(e) {
      this.priceEdit = e.target.value
    },
    onImageFileChange(e) {
      //this.$refs.myFiles.files
      URL.revokeObjectURL(this.imageUrlEdit);
      this.imageUrlEdit = this.$refs['imageUpload'].files[0];//e.target.value
      const url = URL.createObjectURL(this.imageUrlEdit);

      this.imageUrlEdit=url;
     // let files = e.target.files || e.dataTransfer.files;
     // if (!files.length)
    //    return;
     // this.createImage(item, files[0]);
    },
    createImage(item, file) {
      let image = new Image();
      let reader = new FileReader();

      reader.onload = (e) => {
        item.image = e.target.result;
      };
      reader.readAsDataURL(file);
    },
  },
  mounted() {
    // console.log("ready...");
    window.addEventListener('resize', this.handleResize);
  },
  beforeDestroy() {
    // console.log("beforeDestroy...");
    window.removeEventListener('resize', this.handleResize);
 },
}
</script>

<style scoped>
 .buyButton{
   width: 400px;
   height: 50px;
 }
 .product{
   position: relative;
   width: 1500px;
   height: 100%;
   margin: 0 auto;
   padding: 0 30px;
   display:flex ;
   flex-direction:row ;
 }

</style>



