<template>

    <div class="dropdown">

      <button class="dropdown-button" @click="toggleDropdown">▼</button>
      <div class="dropdown-content" v-if="dropdownOpen"><!--class="dropdown-content" class="dropdown-button"▼-->
        <button type="button" class="nes-btn is-primary" @click="logout">로그아웃</button>
        <button type="button" class="nes-btn is-primary" @click="goToProfile">마이프로필</button>
      </div>
    </div>

</template>

<script>
import axios from "axios";
import DropDownButtonComponent from "./DropDownButtonComponent";

export default {
  name: "UserLoginButtonComponent",
  components: {DropDownButtonComponent},
  data() {
    return {
      dropdownOpen: false
    };
  },
  created() {
    this.axios = axios.create({
      baseURL: this.$myUrl
    });
  },
  methods: {
    toggleDropdown() {
      this.dropdownOpen = !this.dropdownOpen;
    },
    logout() {
      try {
        // 데이터 가져오기
        this.axios.post('/Main/Logout')
          .then((response) => {
            this.$router.push('/');
          })
          .catch((error) => {
            this.$alert('Login out', '로그인 아웃 실패');

          });
      } catch(error) {
        // 예외 처리
      }
      // 로그아웃 로직을 구현하세요.
    },
    goToProfile() {
      // 마이프로필 페이지로 이동하는 로직을 구현하세요.
    }
  }
};
</script>

<style scoped>
.menu {
  position: relative;
}

.dropdown {
  position: relative;
  display: inline-block;
  z-index: 1;
}

.dropdown-button {
  border: none;
  padding: 8px;
  font-size: 16px;
  cursor: pointer;
}

.dropdown-content {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  min-width: 160px;
  padding: 8px;
 box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 2;
}

.dropdown-content button {
  display: block;
  width: 100%;
  padding: 8px;
  text-align: left;

  cursor: pointer;
}

.dropdown-content button:hover {
  background-color: #19ce60;
}

.dropdown:hover .dropdown-content {
  display: block;
}
</style>



