<template>
  <div class="nes-container is-dark_cus">

    <ul>
      <li v-for="(product, index) in products" :key="product.id" class="product">
        <div class="media">
          <video :src="product.videoUrl" width="300" height="150"  autoplay></video>
        </div>
        <div class="image-wrapper">
          <img :src="product.imageUrl || emptyImageUrl" alt="product.name" width="150" height="150" />
        </div>
        <div class="product-info">
          <h2 class="nes-text  ">{{ product.productName }}</h2>
          <span class="nes-text ">{{ product.price }} 원</span>
          <span class="nes-text ">{{ product.description }}</span>
          <div class="buttons">
            <button class="buy-button" v-on:click="buyProduct">구매하기</button>
            <button class="cart-button" v-on:click="inputBasket(product)">장바구니에 담기</button>
          </div>
        </div>
      </li>
    </ul>
    <button class="nes-btn is-primary" @click="goToMain">홈으로</button>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "Product",
  data() {
    return {
      products: [],
      emptyImageUrl: 'https://via.placeholder.com/350x150',
    };
  },

  created() {
    this.axios = axios.create({
      baseURL: this.$myUrl
    });
    try {
      this.axios
        .get('/api/Products')
        .then(response => {
          this.products = response.data;
        })
        .catch(error => {

        });
    } catch (error) {
    }
  },
  methods: {
    buyProduct(){

    },
    inputBasket(product){
      try {
        this.axios.post('/Basket/Post', {
          basketId: {
            productId: product.id,
            usersId: "1",
            date: "1"
          },
          productName: product.productName,
          price: product.price,
          count: 1,
          status: "B",
        }).then((response) => {
            this.$alert('장바구니', '장바구니 추가 완료');
          })
          .catch((error) => {
            this.$alert('장바구니', '장바구니 추가 실패');
          });
      } catch (error) {
        // 예외 처리
        console.error(error);
      }
    },
    playVideo(index) {
     // console.log(this.$refs[`productVideo${index}`][0]);
      let videos = this.$refs[`productVideo${index}`];
      if (videos) {
        videos = videos[0];
        videos.play();
      }
    },

    pauseVideo(index) {
      let videos = this.$refs[`productVideo${index}`];
      if (videos) {
        videos = videos[0];
        videos.pause();
      }
    },
    addToCart(index) {
      // 장바구니에 비디오 추가하는 로직
    },
    buyVideo(index) {
      // 비디오를 구매하는 로직
    },
    goToMain() {
      this.$router.push('/Main');
    },

  },
}
</script>

<style scoped>
.product {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.media {
  margin-right: 20px;
}

.media video {
  margin-bottom: 10px;
}

.product-info {
  flex-grow: 1;
}

.product-info h2 {
  margin-bottom: 10px;
}

.product-info p {
  margin: 0;
}

.product-info .price {
  font-weight: bold;
  font-size: 18px;
}

.product-info .description {
  margin-bottom: 10px;
}

.buttons {
  display: flex;
  gap: 10px;
}
.image-wrapper {
  margin-left: 5px;
}
.buy-button {
  background-color: #008CBA;
  color: #fff;
  border: none;
  border-radius: 5px;
  padding: 10px;
  font-size: 16px;
  cursor: pointer;
}

.cart-button {
  background-color: #f2f2f2;
  color: #333;
  border: none;
  border-radius: 5px;
  padding: 10px;
  font-size: 16px;
  cursor: pointer;
}
</style>



