<template>
  <div>
    <div>
      <h2>
        <div :style="{ transform: 'rotate('+ turn+'turn)'}"> Test </div>
        <span :style="cssRotationWord">
          KSH
          <span :style="cssRotationWord" >K</span>
          <span :style="cssRotationWord" >S</span>
          <span :style="cssRotationWord" >H</span>
        </span>
      </h2>
    </div>
  </div>
</template>

<script>
export default {
  name: "MainBody",
  data() {
    return {
      turn: 0.5
    }
  },
  computed: {
    cssRotationWord(){
      return{
        opacity:1,
        float: "bottom",
        fontSize: "200px",
        color: "white",
        transform: 'rotate(' + this.turn + 'turn)'
      }
    }
  }
}
</script>

<style scoped>





</style>



