<template>
  <div>
    <div class="nes-container is-dark_cus">
      <button class="nes-btn is-primary" v-for="menu in menus" :key="menu.id" @click="goTo(menu.route)" >{{ menu.label }}</button>
    </div>

  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "Admin",
  data () {
    return {
      menus: [
        { id: 1, label: '홈', route: '/Main' },
        { id: 2, label: '상품관리', route: '/Admin/Product' },
        { id: 3, label: '게시판관리', route: '/Admin/Post' },
        { id: 4, label: '고객센터관라', route: '/Admin/Customer' },
      ],
    }
  },
  created() {
    this.axios = axios.create({
      baseURL: this.$myUrl
    });
  },
  methods: {

    goTo(route) {
      // Vue Router를 사용하여 페이지 이동

      this.axios = axios.create({
        baseURL: this.$myUrl
      });
      try{
        // /Admin/Product
        this.axios.get(route)
          .then(response => {
            this.$router.push(route).then(() => {

            });

          })
          .catch(error => {
          });

      }catch (error){
      }
    },
  },
}
</script>

<style scoped>
.horizontal_area{
  float: left;
}
.slider_area{
  display: flex; /* set the display property to flex */
  align-items: center; /* center the child elements vertically */
}
.menu-container {

  top: 0;
  width: 100%;
  display: flex;
  justify-content: space-around;
  background-color: #f0f0f0;
  padding: 10px 0;
}
.menu-button {
  border: none;
  background: linear-gradient(to bottom, #00bfff 0%, #0099cc 100%);
  font-size: 16px;
  font-weight: bold;
  color: #fff;
  cursor: pointer;
  padding: 10px;
  transition: all 0.2s ease-in-out;
}

.menu-button:hover {
  background: linear-gradient(to bottom, #0099cc 0%, #00bfff 100%);
  transform: translateY(-3px);
  box-shadow: 0px 2px 3px rgba(0, 0, 0, 0.2);
}
.menu-container button {
  border: none;
  background-color: transparent;
  font-size: 16px;
  font-weight: bold;
  color: #333;
  cursor: pointer;
  padding: 10px;
  transition: background-color 0.2s ease-in-out;
}

.menu-container button:hover {
  background-color: #ddd;
}
</style>



