<template>
  <div>
    <div class="border_area">
      <div>정렬 방식:</div>
      <DropDownButtonComponent id="dropDown1" :categories="categories1" @CustomEventChanged="(msg) => this.shapeProduct = msg"/>
      <div>권한 설정:</div>
      <DropDownButtonComponent id="dropDown2" :categories="categories2" @CustomEventChanged="(msg) => this.authority = msg"/>
      <div>상품 갯수:</div>
    </div>
       <div :style="cssProducts">  <!--db 에서 데이터를 가져오고 렌더링 해줄 상품들 갯수 카운팅 -->
       <ProductComponent :p-authority="this.authority" :p-product-number="'000000001'"/>
       <ProductComponent :p-authority="this.authority" :p-product-number="'000000002'"/>
       <ProductComponent :p-authority="this.authority" :p-product-number="'000000003'"/>
       <ProductComponent :p-authority="this.authority" :p-product-number="'000000004'"/>
       <ProductComponent :p-authority="this.authority" :p-product-number="'000000005'"/>
       <ProductComponent :p-authority="this.authority" :p-product-number="'000000006'"/>
    </div>
  </div>
</template>

<script>
import ProductComponent from "../Product/ProductComponent";
import DropDownButtonComponent from "../commonComponents/DropDownButtonComponent";
export default {
  name: "TesterProduct",
  components: {DropDownButtonComponent, ProductComponent},
  data() {
    return {
      shapeProduct: 'column',
      authority:'',

      categories1: [
        {id: 'column', name: "수직"},
        {id: 'row', name: "수평"},
      ],
      categories2: [
        {id: 'admin', name: "관리자"},
        {id: 'custom', name: "고객"},
      ],
    }
  },
  computed: {
    cssProducts() {
      return {
        display: 'flex',
        flexDirection: this.shapeProduct,
      }
    }
  }
}
</script>

<style scoped>
.border_area {
  width: 300px;
  height: 300px;
  background: gray;
}
</style>



