<template>
  <div id="cover" class="cover-left"></div>
</template>

<script>
export default {
  name: "Cover",
  computed: {
    CssCoverEffect() {
      return `transform: translateX(${this.xParent}px) translateY(${this.yParent}px) translateZ(0) translate3d(0, 0, 0);`
    }
  }
}
</script>

<style scoped>
#cover {
  background: #e7e7e7;
  z-index: 9999999;
  position: fixed;
  top: 0%;
  left: -25%;
  width: 150%;
  height: 100%;
  transform-origin: left;
}
#cover.cover-left {
  animation: coverLeft 2s cubic-bezier(0.77,0,0.175,1) both;
}
@keyframes coverLeft {
  0% {
    transform: scaleX(1) skewX(30deg);
    transform-origin: left;
  }
  100% {
    transform: scaleX(0) skewX(30deg);
    transform-origin: left;
  }
 }
</style>



