<template>
  <div>
    <h2>Product Registration</h2>
    <form @submit.prevent="registProduct">
      <div>
        <label for="productName">Product Name:</label>
        <input type="text" id="productName" v-model="productName" required>
      </div>
      <div>
        <label for="price">Price:</label>
        <input type="number" id="price" v-model="price" required>
      </div>
      <div>
        <label for="imageUrl">Image URL:</label>
        <input type="text" id="imageUrl" v-model="imageUrl" required>
      </div>
      <div>
        <label for="videoUrl">VideoUrl URL:</label>
        <input type="text" id="videoUrl" v-model="videoUrl" required>
      </div>
      <div>
        <label for="description">Description:</label>
        <textarea id="description" v-model="description" required></textarea>
      </div>
      <button class="nes-btn is-primary" type="submit">등록</button>
      <button  class="nes-btn is-primary" @click="gotoAdminProductList">목록으로</button>
    </form>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "AdminProductRegist",
  data() {
    return {
      productName:'',
      price: '',
      imageUrl: '',
      videoUrl: '',
      description: '',
    }
  },
  created() {
    this.axios = axios.create({
      baseURL: this.$myUrl
    });
  },
  methods: {
    registProduct(event) {
      event.preventDefault(); // submit 이벤트의 기본 동작인 폼 제출을 막음
      const data = {
        productName: this.productName,
        price: this.price,
        imageUrl: this.imageUrl,
        videoUrl:this.videoUrl,
        description: this.description,
      };
      const config = {
        headers: {
          'Content-Type': 'application/json'
        }
      };
      try {
        this.axios.post('/Products/Post', data, config)
          .then((response) => {
            this.$router.go(0);
            this.$alert('상품', '등록 성공');
          })
          .catch((error) => {
            this.$alert('상품', '등록 실패');
          });
      } catch (error) {
        // 예외 처리
        console.error(error);
      }
    },
    gotoAdminProductList() {
      this.$router.push('/Admin/Product');
    },
  }
}
</script>

<style scoped>
form div {
  margin: 10px 0;
}
form label {
  display: inline-block;
  width: 150px;
}
form input[type="text"],
form input[type="number"],
form textarea {
  width: 300px;
}
</style>



