<template>
  <div id="group_tags">
    <div id ="category_ui" class="inner_category">
      <ul class="list_category type_fix">
        <li class ="category_item">메일</li>
        <li class ="category_item">카페</li>
        <li class ="category_item">블로그</li>
        <li class ="category_item">지식iN</li>
        <li class ="category_item">쇼핑</li>
        <li class ="category_item">쇼핑</li>
        <li class ="category_item">Pay</li>
        <li class ="category_item">Tv</li>
      </ul>
    </div>
  </div>
</template>
<script >
import ImageSliderComponent from "../commonComponents/ImageSliderComponent";
import ImageCropComponent from "../commonComponents/ImageCropComponent";
export default {
  name: "MainTopCategory",
  components: {ImageCropComponent, ImageSliderComponent},
  data () {
    return {
      rotation: 0,
      imageCrop:'https://ssl.pstatic.net/static/weather/image/sp_icon_weather_346e83fd.png',
      imgList: [
        'https://www.shutterstock.com/blog/wp-content/uploads/sites/5/2019/09/shutterstock_1151676383.jpg?w=2000',
        'https://www.shutterstock.com/blog/wp-content/uploads/sites/5/2019/09/shutterstock_1151632343.jpg?w=2000',
        'https://www.shutterstock.com/blog/wp-content/uploads/sites/5/2019/09/shutterstock_1429964489.jpg?w=2000'
      ],
    }
  },
  methods: {
    rotateRight() {
      this.rotation -= 90
    },
    rotateLeft() {
      this.rotation += 90
    }
  }
}
</script>

<style scoped>
.transition {
  transition: transform 0.5s ease-in-out;
}
 .category_item{
   float:left;
   padding: 5px ;
 }
 .eg-filck-viewPort{
   position: relative;
   z-index: 0;
   overflow: hidden;
   min-width: 100%;
   height: 100%;
   touch-action: pan-x;
   user-select: none;
   -webkit-user-drag: none;
   width: 270px;
 }
 .group_weather{
   position: absolute;
   top: 0;
   right: 30px;
   bottom: 0;
   overflow: hidden;
   max-width: 270px;
   height: 52px;
   text-align: right;
 }
.inner_category {
   position: relative;
   width: 1130px;
   padding: 0 30px;
   margin: 0 auto;
 }
#group_tags{
  position: relative;
  -webkit-box-shadow: 0 1px 3px 0 rgb(0 0 0 / 12%);
  box-shadow: 0 1px 3px 0 rgb(0 0 0 / 12%);
  color: #03c75a;
}
 .list_category.type_fix  {
   color: #03c75a;
 }
 .list_category.MyFunction{
   margin-left: 9px;
 }
</style>



