<template>
  <div class="nes-container is-dark_cus flex-container"><!---->
      <!-- 게임 요소를 추가하세요 -->
    <h1 class="nes-text" for="dark_field">
      <i class="nes-octocat animate"></i>
      <span class="nes-text">LOGIN</span>
    </h1>
    <input type="text" class="nes-input is-dark" name="username" v-model="input.id" placeholder="Username" />
    <input type="password" class="nes-input is-dark" name="password" v-model="input.password" placeholder="Password" />
    <div class="button-container">
      <button type="button" class="nes-btn is-primary" v-on:click="login()"><span class="nes-text">로그인</span></button>
      <!-- <button type="button" class="nes-btn is-primary" v-on:click="SignUp()"><span class="nes-text">회원가입</span></button>-->
      <button type="button" class="nes-btn is-primary" v-on:click="ShowDiagram()"><span class="nes-text">db 다이어그램</span></button>
      <!-- <button type="button" class="nes-btn is-primary" v-on:click="ShowAdmin()"><span class="nes-text">어드민계정</span></button>-->
      <button type="button" class="nes-btn is-primary" v-on:click="ShowCustom()"><span class="nes-text">고객계정</span></button>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import AlertNess from "./AlertNess";

export default {
  name: "SignIn",
  components: {
    AlertNess,
  },
  data() {

    return {
      input: {
        id: "",
        password: "",
        email :"",
        level :100,
      }
    }
  },
  created() {
    this.axios = axios.create({
      baseURL: this.$myUrl
    });
  },
  methods: {
    login() {
      try {
        // 데이터 가져오기
        //this.$showAlert('Custom Alert Title', 'This is a custom alert message.');

        this.axios.post('/loginProxy', {
          id: 0,
          password: this.input.password,
          username: this.input.id,
          email: 'emailPost',
          name: 'namePost',
          level: this.input.level,

        })
          .then((response) => {
            this.$alert('Login', '로그인 성공');
            this.$router.push('/Main').then(() => {
            });

          })
          .catch((error) => {
            this.$alert('Login', '로그인 실패');
          });
      } catch (error) {
        // 예외 처리
        console.error(error);
      }

    },
    SignUp() {

      try {
        // 데이터 가져오기
        this.axios.get('/SignUp')
          .then((response) => {
            this.$router.push('/SignUp').then(() => {

            });
          })
          .catch((error) => {
            this.$alert('회원가입', '회원가입 실패');

          });
      } catch (error) {
        // 예외 처리
        console.error(error);
      }
    },
    ShowDiagram() {
      this.$popupImage('db 다이어그램 ', '../../assets/img/db.png');
    },
    ShowAdmin() {
      this.$alert('어드민 계정', '사용자: 비밀번호:');
    },
    ShowCustom() {
      this.$alert('고객 계정', '아이디:test , 비밀번호:1234');
    },

  }
}
</script>
<style scoped>
.flex-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  height: 100vh; /* Optional: Adjust this value to control the vertical centering */
}
.button-container {
  display: flex;
  justify-content: center;
  margin-top: 16px;
}

#login {
  width: 500px;
  border: 1px solid #CCCCCC;
  background-color: #FFFFFF;
  margin: auto;
  margin-top: 200px;
  padding: 20px;
}

</style>



