<template>
  <div class="nes-container is-dark_cus">
    <h2>게시글 보기</h2>
    <form>
      <div class="form-group">
        <label for="title">제목</label>
        <span type="text" class="nes-text is-dark" id="title">{{ post.title}}</span>
      </div>
      <div class="form-group">
        <label for="content">내용</label>
        <span type="text"  class="nes-text is-dark" id="contents">{{post.contents}}</span>
      </div>
      <div class="form-group email-group">
        <label for="email">이메일</label>
        <div class="email-input-container">
          <span type="text"  class="nes-text is-dark" id="email">{{post.email}}</span>
        </div>
      </div>
      <div class="form-group">
        <label for="files">파일</label>
        <!--
        <div class="file-input-container">
          <input type="file" multiple class="form-controls file-input" id="files" @change="onFileChange">
        </div>-->
      </div>
      <button class="nes-btn is-primary" v-on:click="goToList">목록으로</button>
      <button class="nes-btn is-primary" v-on:click="goToDelete(post.id)">삭제</button>

    </form>
  </div>
</template>

<style>
.form-group {
  margin-bottom: 20px;
}
.email-input-container{
  display: flex;
}

.file-input-container {
  width: 300px;
}

label {
  font-weight: bold;
}
textarea {
  height: auto;
  min-height: 300px;
  resize: none;
}
.email-input {

  display: flex;
  margin-bottom: 20px;
  width: 200px;
}
.email-domain-select {
  width: 150px;
  font-size: 14px;
  height: 33px;
  margin-left: 10px;
}
.form-controls {

  border-radius: 0;
  border: none;
  border-bottom: 1px solid #eee;
  box-shadow: none;
  width: 100%;
}
.form-controls:focus {

  border-color: #007bff;
  box-shadow: none;
}
.btn {
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 0;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: bold;
  transition: background-color 0.3s ease-in-out;
}
.btn:hover {
  background-color: #0069d9;
  color: #fff;
}
</style>

<script>
import axios from "axios";

export default {
  name: "AdminPostDetail",

  data() {
    return {
      post:  [],
    };
  },

  created() {
    const postId = this.$route.params.id;
    this.axios = axios.create({
      baseURL: this.$myUrl
    });

    try {
      this.axios
        .get(`/api/Detail/${postId}`)
        .then(response => {
          this.post = response.data;
        })
        .catch(error => {
          this.$router.push('/Admin/Post');

        });
    } catch (error) {
      this.$router.push('/Admin/Post');

    }
  },

  methods: {
    goToList() {
      this.$router.push('/Admin/Post');
    },
    goToDelete(id) {
      this.axios = axios.create({
        baseURL: this.$myUrl
      });
      try {
        const postId = id;
        this.axios.delete(`/Admin/Notice/Delete/${postId}`);
        this.$alert('게시판', '삭제 성공');

        this.$router.push('/Admin/Post');

      }catch (error) {
        this.$alert('게시판', '삭제 오류');
      }
    }
  }
};
</script>



