<template>
  <div :class="[ 'g-cursor', { 'g-cursor_hover': hover }, {'g-cursor_hide': hideCursor} ]">
    <div :style="cursorCircle" class="g-cursor__circle"></div>
    <div class="g-cursor__point" ref="point" :style="cursorPoint"></div>
  </div>
</template>

<script>
export default {
  name: "mouseComponent",
  data() {
    return {
      xChild: 0,
      yChild: 0,
      xParent: 0,
      yParent: 0,
      hover: false,
      hideCursor: true,
      mWidth: 30,
      mHeight: 30,
    }
  },
  props:{
    pointerColor:{
      type:String,
      default:"fff"
    },
    borderColor:{
      type:String,
      default:"fff"
    },
    borderShape:{
      type:Number,
      default:100
    },

  },
  computed: {
    cursorCircle() {
      return {
        transform:"translateX("+this.xParent+"px) translateY("+this.yParent+"px) translateZ(0) translate3d(0, 0, 0)",
        border: 2 +"px solid #"+this.borderColor,
        "--mHeight": this.mHeight+"px",
        "--mWidth": this.mWidth+"px",
        borderRadius:this.borderShape+"%",
      }
    },
    cursorPoint() {
      return {
        transform: "translateX(" + (this.xChild-3) + "px) translateY(" + (this.yChild-3) + "px) translateZ(0) translate3d(0, 0, 0)",
        background:"#"+this.pointerColor
      }
    },
  },
  methods: {
    moveCursor(e) {
      this.xChild = e.clientX;
      this.yChild = e.clientY;

      setTimeout(() => {
        this.xParent = e.clientX - this.mWidth/2;//15
        this.yParent = e.clientY - this.mHeight/2;//15
      }, 100);
    }
  },
  mounted() {
    document.addEventListener("mousemove", this.moveCursor);
    document.addEventListener('mouseleave', e => {
      this.hideCursor = true;
    });
    document.addEventListener('mouseenter', e => {
      this.hideCursor = false;
    });
    document.addEventListener('mouseup', e => {
      this.mHeight=30;
      this.mWidth=30;
      this.xParent = e.clientX - this.mWidth/2;//15
      this.yParent = e.clientY - this.mHeight/2;//15
      this.moveCursor(e);
    });
    document.addEventListener('mousedown', e => {
      this.mHeight=60;
      this.mWidth =60;
      this.xParent = e.clientX - this.mWidth/2;//15
      this.yParent = e.clientY - this.mHeight/2;//15
      this.moveCursor(e);
    });
  }
}
</script>

<style scoped>
:root{
  --mWidth:var;
  --mHeight:var;
}
.g-cursor_hide{
  opacity: 0;
  width: 0px;
  height: 0px;
  transition: width .6s ease,
  height .6s ease,
  opacity .6s ease;
}
.g-cursor__circle{

  pointer-events: none;
  user-select: none;
  top: 0;
  left: 0;
  position: fixed;

  width: var(--mWidth);
  height: var(--mHeight);
   /*
  border-radius: 100%;
*/
  z-index: 5555;
  backface-visibility: hidden;
  transition: opacity .6s ease;
}
.g-cursor__point{
  top: 0;
  left: 0;
  position: fixed;
  width: 10px;
  height: 10px;
  pointer-events: none;
  user-select: none;
  border-radius: 100%;/*100*/
  z-index: 55555555;
  backface-visibility: hidden;
  will-change: transform;
}
.g-cursor_hover  g-cursor__circle{
  opacity: 0;
  width: 60px;  /*60*/
  height: 60px; /*60*/
  transition: width .6s ease,
  height .6s ease,
  opacity .6s ease;

}
</style>



