<template>
  <div class="nes-container is-dark_cus">
    <h2>게시글 작성</h2>
    <form>
      <div class="form-group">
        <label for="title">제목</label>
        <input type="text"  class="nes-input is-dark" id="title" v-model="post.title" placeholder="제목을 입력하세요">
      </div>
      <div class="form-group">
        <label for="content">내용</label>
        <!--class="form-controls" id="content"-->
        <textarea id="textarea_field" class="nes-textarea is-dark" maxlength="100" v-model="post.contents"  placeholder="내용을 입력하세요"></textarea>
      </div>
      <div class="form-group email-group">
        <label for="email">이메일</label>
        <div class="email-input-container">
          <input type="text" class="nes-input is-dark" id="email" v-model="post.email" placeholder="이메일을 입력하세요">
          <div class="nes-select is-dark">
           <select v-model="post.emailDomain" required id="dark-select"><!--email-domain-select-->
              <option value="">선택하세요</option>
              <option value="@naver.com">@naver.com</option>
              <option value="@gmail.com">@gmail.com</option>
              <option value="@daum.net">@daum.net</option>
              <option value="etc">직접입력</option>
            </select>
          </div>
        </div>
      </div>
      <div class="form-group">
        <label for="files">파일</label>
        <div class="nes-input is-dark"><!---->
          <input type="file" multiple class="form-controls file-input" id="files" @change="onFileChange">
        </div>
      </div>
      <button class="nes-btn is-primary" @click="goToList">목록으로</button>
      <button type="submit" class="nes-btn is-primary" @click.prevent="submitPost">작성 완료</button>
    </form>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "PostForm",
  data() {
    return {
      post: {
        title: "",
        contents: "",
        email: "",
        emailDomain: "",
        username:"",
        createdDate:"",
        files: []
      },
      maxLength: 100 // 최대 글자 수
    };
  },
  created() {
    this.axios = axios.create({
      baseURL: this.$myUrl
    });
  },
  methods: {
    limitText() {
      if (this.post.contents.length > this.maxLength) {
        this.post.contents = this.post.contents.substring(0, this.maxLength);
      }
    },
    onFileChange(event) {
      this.post.files = event.target.files;
      const fileName = event.target.files[0].name;
      document.querySelector('.file-input-text').textContent = fileName;
    },
    goToList() {
      this.$router.push('/PostList');
    },
    submitPost() {
      const data = {
        title: this.post.title,
        contents: this.post.contents,
        email: this.post.email+this.post.emailDomain,
        username:this.post.username,
        createdDate: this.post.createdDate,
        files :this.post.files,
      };
      const config = {
        headers: {
          'Content-Type': 'application/json'
        }
      };
      try {
        this.axios
          .post('/Notice/Post', data,config)
          .then(response => {
            this.$alert('게시판', '등록 성공');
            this.$router.push('/PostList');
          })
          .catch(error => {
            this.$alert('게시판', '등록 실패');

          });
      } catch (error) {
      }
    },
  },
  computed: {
    limitedText(text) {
      const maxLength = 100;
      if (text.length > maxLength) {
        return text.substring(0, maxLength) + '...';
      }
      return text;
    },

  }
};
</script>

<style>
.form-group {
  margin-bottom: 20px;
}
.email-input-container{
  display: flex;
}

.file-input-container {
  width: 300px;
}

label {
  font-weight: bold;
}
textarea {
  height: auto;
  min-height: 300px;
  resize: none;
}
.email-input {
  background: black;
  display: flex;
  margin-bottom: 20px;
  width: 200px;
}
.email-domain-select {
  width: 150px;
  font-size: 14px;
  height: 33px;
  margin-left: 10px;
}
.form-controls {
  background: black;
  border-radius: 0;
  border: none;
  border-bottom: 1px solid #eee;
  box-shadow: none;
  width: 100%;
}
.form-controls:focus {

  border-color: #007bff;
  box-shadow: none;
}
.btn {
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 0;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: bold;
  transition: background-color 0.3s ease-in-out;
}
.btn:hover {
  background-color: #0069d9;
  color: #fff;
}
</style>



