<template>
  <div class="nes-container is-dark_cus">
    <ul>
      <li v-for="(product, index) in products" :key="product.id" class="product">
        <div class="media">
          <video :src="product.videoUrl" width="300" height="150"  autoplay></video>
        </div>
        <div class="image-wrapper">
          <img :src="product.imageUrl || emptyImageUrl" alt="product.name" width="150" height="150" />
        </div>
        <div class="product-info">
          <h2 class="nes-text">{{ product.productName }}</h2>
          <p class="nes-text">{{ product.price }} 원</p>
          <p class="nes-text">{{ product.description }}</p>
          <div class="buttons">
            <button class="buy-button"@click="modifyProduct(product.id)">수정</button>
            <button class="cart-button"@click="deleteProduct(product.id)">삭제</button>
          </div>
        </div>
      </li>
    </ul>
    <button class="nes-btn is-primary" @click="registProduct">등록</button>
    <button class="nes-btn is-primary" @click="gotoAdmin">어드민으로</button>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "AdminProduct",
  data() {
    return {
      products: [],
      emptyImageUrl: 'https://via.placeholder.com/350x150',
    };
  },

  created() {
    this.axios = axios.create({
      baseURL: this.$myUrl
    });
    try {
      this.axios
        .get('/api/Products')
        .then(response => {
          this.products = response.data;
        })
        .catch(error => {
        });
    } catch (error) {
    }
  },
  methods: {
    playVideo(index) {
      // console.log(this.$refs[`productVideo${index}`][0]);
      let videos = this.$refs[`productVideo${index}`];
      if (videos) {
        videos = videos[0];
        videos.play();
      }
    },

    pauseVideo(index) {
      let videos = this.$refs[`productVideo${index}`];
      if (videos) {
        videos = videos[0];
        videos.pause();
      }
    },
    addToCart(index) {
      // 장바구니에 비디오 추가하는 로직
    },
    buyVideo(index) {
      // 비디오를 구매하는 로직
    },
    gotoAdmin() {;

      this.$router.push('/Admin');

    },
    deleteProduct(id) {
      const postId = id;
      this.axios = axios.create({
        baseURL: this.$myUrl
      });
      try {
        this.axios.delete(`/Admin/Product/Delete/${postId}`);
        this.$alert('상품', '상품 성공');
        this.$router.go(0);
       // this.$router.push('/Admin/Product');

      }catch (error) {
        this.$alert('상품', '상품 실패');
      }
    },
    modifyProduct(index) {
      try{
        this.$router.push({
          name: "AdminProductDetail",
          params: {
            id: index,
          },
        });
      }catch (error){
      }
    },
    registProduct() {
      this.$router.push('/Admin/ProductRegist');
    },
  },
}
</script>

<style scoped>
.product {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.media {
  margin-right: 20px;
}

.media video {
  margin-bottom: 10px;
}

.product-info {
  flex-grow: 1;
}

.product-info h2 {
  margin-bottom: 10px;
}

.product-info p {
  margin: 0;
}

.product-info .price {
  font-weight: bold;
  font-size: 18px;
}

.product-info .description {
  margin-bottom: 10px;
}

.buttons {
  display: flex;
  gap: 10px;
}
.image-wrapper {
  margin-left: 5px;
}
.buy-button {
  background-color: #008CBA;
  color: #fff;
  border: none;
  border-radius: 5px;
  padding: 10px;
  font-size: 16px;
  cursor: pointer;
}

.cart-button {
  background-color: #f2f2f2;
  color: #333;
  border: none;
  border-radius: 5px;
  padding: 10px;
  font-size: 16px;
  cursor: pointer;
}
</style>



