<template>
  <div>
    <h2>Product Modify</h2>
    <form @submit.prevent="registProduct">
      <div>
        <label for="productName">Product Name:</label>
        <input type="text" id="productName" v-model="post.productName" required>
      </div>
      <div>
        <label for="price">Price:</label>
        <input type="number" id="price" v-model="post.price" required>
      </div>
      <div>
        <label for="imageUrl">Image URL:</label>
        <input type="text" id="imageUrl" v-model="post.imageUrl" required>
      </div>
      <div>
        <label for="videoUrl">VideoUrl URL:</label>
        <input type="text" id="videoUrl" v-model="post.videoUrl" required>
      </div>
      <div>
        <label for="description">Description:</label>
        <textarea id="description" v-model="post.description" required></textarea>
      </div>
      <button class="nes-btn is-primary" type="submit" >수정 완료</button>
      <button class="nes-btn is-primary" @click="gotoAdminProductList">목록으로</button>
    </form>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "AdminProductDetail",
  data() {
    return {
      post:  [
        {
          productName:"",
          price: "",
          imageUrl: "",
          videoUrl: "",
          description:"",
        }
      ]
    }
  },
  created() {
    const postId = this.$route.params.id;
    this.axios = axios.create({
      baseURL: this.$myUrl
    });
    try {
      this.axios
        .get(`/api/DetailProduct/${postId}`)
        .then(response => {
          this.post = response.data;
        })
        .catch(error => {
          this.$router.push('/Admin/AdminProduct');
        });
    } catch (error) {
      this.$router.push('/Admin/AdminProduct');

    }
  },
  methods: {
    registProduct(event) {
      event.preventDefault(); // submit 이벤트의 기본 동작인 폼 제출을 막음
      const postId = this.$route.params.id;
      const data = {
        productName: this.post.productName,
        price: this.post.price,
        imageUrl: this.post.imageUrl,
        videoUrl:this.post.videoUrl,
        description: this.post.description,
      };
      const config = {
        headers: {
          'Content-Type': 'application/json'
        }
      };
      try {
        this.axios.patch(`/api/Products/Patch/${postId}`, data, config)
          .then((response) => {
            this.$router.go(0);
            this.$alert('상품', '수정 성공');

          })
          .catch((error) => {
            this.$alert('상품', '수정 실패');

          });
      } catch (error) {
        // 예외 처리
        console.error(error);
      }
    },
    gotoAdminProductList() {
      this.$router.push('/Admin/Product');
    },
  }
}
</script>

<style scoped>
form div {
  margin: 10px 0;
}
form label {
  display: inline-block;
  width: 150px;
}
form input[type="text"],
form input[type="number"],
form textarea {
  width: 300px;
}
</style>



