<!-- Alert.vue -->
<template>
  <div div v-if="visible" class="modal"> <!--<div v-if="visible" class="modal">-->
    <div class="modal-content">
      <h3 class="nes-text"> {{ title}}</h3>
      <img style="height: 500px" src="../../assets/img/db.png" >
       <button  class="nes-btn is-primary" @click="closeAlert">OK</button>

     </div>
   </div>
 </template>

 <script>
 export default {
   name:'Popup',

   props: {
     visible: {
       type: Boolean,
       default: true
     },
     title: {
       type: String,
       default: ''
     },
     imgurls: {
       type: String,
       default: ''
     }
   },
   methods: {
     closeAlert() {
       this.visible = false;
       this.$emit('close');
     },
   },
 };
 </script>

 <style scoped>
 .modal {
   position: fixed ;

   top: 0;
   left: 0;
   right: 0;
   bottom: 0;
   background-color: rgba(0, 0, 0, 0.5);
   display: flex;
   justify-content: center;
   align-items: center;
 }

 .modal-content {

   width: 600px;
   height: 650px;
   background-color: white;
   padding: 20px;
   border-radius: 8px;
   text-align: center;
 }

 </style>



