<template>
  <div class="ico"><!--div id="logo" class="cropped_logo"-->
    <img :src=pImage>
  </div>
</template>

<script>
export default {
  name: "ImageCropComponent",
  props:{
    pImage: {
      type: String,
      default: ''
    },
  }
}
</script>

<style scoped>
.ico {
  width: 34px;
  height: 34px;
  display: inline-block;
  vertical-align: top;
  scale: 0.2;
  margin: 0 4px;
}
</style>



