            <li>&nbsp;&nbsp;&nbsp;* front-end : vue.js</li>
            <li>&nbsp;&nbsp;&nbsp;* DB: h2-console,h2Database,HikariCP:4.0.3 </li>
            <li>&nbsp;&nbsp;&nbsp;* DevOps: aws e2c, aws router52, 가비아</li>
