
<template>
  <div class="nes-container is-dark_cus">
  <form class="nes-container" @submit.prevent="registerUser">
    <div class="nes-field is-inline">
      <h2 class="nes-field " for="Id">Username</h2>
    </div>
      <input  class="nes-input is-dark" type="text" id="Id"  v-model="username">

    <div class="nes-field is-inline">
      <h2 class="nes-field " for="password">Password</h2>
    </div>
      <input  class="nes-input is-dark" type="password" id="password"  v-model="password">

    <div class="nes-field is-inline">
      <h2 class="nes-field " for="password">Password confirm</h2>
    </div>
    <input  class="nes-input is-dark" type="password" id="password_confirm"  v-model="password_confirm">
    <p v-if="isPasswordMatch">비밀번호가 일치합니다.</p>
    <p v-else>비밀번호가 일치하지 않습니다.</p>
    <div class="nes-field is-inline">
      <h2 class="nes-field " for="name">name</h2>
    </div>
      <input class="nes-input is-dark" type="text" id="name"  v-model="name">

    <div class="nes-field is-inline">
      <h2 class="nes-field " for="age">age</h2>
    </div>
      <input  class="nes-input is-dark" type="text" id="age" v-model="age">

    <div class="nes-field is-inline">
      <h2 class="nes-field " for="email">email</h2>
    </div>
      <input class="nes-input is-dark" type="text" id="email"  v-model="email">

    <div class="form-group">
      <button  class="nes-btn is-primary"v-on:click="registerUser">Register</button>
      <button  class="nes-btn is-primary" v-on:click="gotoLogin">Login</button>
    </div>


  </form>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "SignUp",
  data() {
    return {
      username:'',
      name: '',
      password: '',
      age: '',
      email: '',
      password_confirm:'',
    }
  },
  created() {
    this.axios = axios.create({
      baseURL: this.$myUrl
    });
  },
  methods: {
    gotoLogin(){
      this.$router.push('/').then(() => {
      });
    },
    registerUser(event) {
      if(this.password!==this.password_confirm){
        this.$alert('회원가입', '회원가입 실패');
        return ;
      }
      event.preventDefault(); // submit 이벤트의 기본 동작인 폼 제출을 막음
      const data = {
        username:this.username,
        name: this.name,
        password: this.password,
        age: this.age,
        email: this.email,
        level: 1,
        bCheck:false,
      };
      const config = {
        headers: {
          'Content-Type': 'application/json'
        }
      };
      try{
        this.axios.post('/SignUp', data, config)
          .then(response => {
            this.$alert('회원가입', '회원가입 성공');
            // HTTP 요청 성공 시 수행할 로직 작성
            this.$router.push('/').then(() => {
            });

          })
          .catch(error => {
            this.$alert('회원가입', '회원가입 실패');
            // HTTP 요청 실패 시 수행할 로직 작성

          });
      }catch(error){
      }

      // 여기서부터 회원가입 로직을 작성하면 됩니다.
    }
  },
  computed: {
    isPasswordMatch() {
      if(this.password === ''||this.password_confirm===''){
        return false;
      }

      return this.password === this.password_confirm;
    },
  },
}
</script>

<style>
.signup-form {
  max-width: 400px;
  margin: 0 auto;
  padding: 20px;
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.form-group {
  margin-bottom: 20px;
}

label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
}

.form-control {

  display: block;
  width: 100%;
  height: 300px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
  outline: none;
}

.btn {
  display: inline-block;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: bold;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.btn-primary {
  background-color: #007bff;
}

.btn-primary:hover {
  background-color: #0069d9;
}
</style>



