<template>
  <div :style="cssDropBox">
    <!--Bind select value with form.category-->
    <select name="options" @change="onChange($event)" v-model="form.category" >
      <!--Add an empty (no value) option-->
      <option :value="null" hidden>{{categoryName}}</option>
      <!--Add options when value is id-->
      <option v-for="category in categories"
              :key="category.id"
              :value="category.id"
      >
        {{category.name}}
      </option>
    </select>
    <!--See how the state change when option select change-->
  </div>
</template>

<script>
export default {
  name: "DropDownButtonComponent",
  data () {
    return{
      form:{
        category: null
      },
      paramData:null
    }
  },
  props:{
    categories: {
      Type:Array,
      default:()=>[
       {id: 1, name:"Category 1"},
       {id: 2, name:"Category 2"},
       {id: 3, name:"Category 3"}
     ]
    },
    categoryName:{
      Type:String,
      default:"Default"
    },
    cssDropBox:{
      width:60+'px',
      height:60+'px',
    }
  },
  methods: {
    onChange(event) {

      this.paramData=event.target.value;
      //return value parent component
      this.$emit('CustomEventChanged', this.paramData);
    },
  },
}
</script>

<style scoped>

</style>



