// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import alertNess from "./components/commonComponents/AlertNess";
import Popup from "./components/commonComponents/Popup";

import('./assets/main.css')
import('./assets/styles/common.css')
import ('nes.css/css/nes.min.css')
import('./assets/styles/background.css')

Vue.config.productionTip = false

Vue.prototype.$alert = function(title, content) {
  const CustomAlertConstructor = Vue.extend(alertNess);
  const customAlertInstance = new CustomAlertConstructor({
    propsData: {
      title,
      content,
    },
  });

  customAlertInstance.$mount();
  document.body.appendChild(customAlertInstance.$el);
  customAlertInstance.$on('close', () => {
    document.body.removeChild(customAlertInstance.$el);
  });
};

Vue.prototype.$popupImage = function(title, contents) {
  const CustomAlertConstructor = Vue.extend(Popup);
  const customAlertInstance = new CustomAlertConstructor({
    propsData: {
      title,
      contents,
    },
  });

  customAlertInstance.$mount();
  document.body.appendChild(customAlertInstance.$el);
  customAlertInstance.$on('close', () => {
    document.body.removeChild(customAlertInstance.$el);
  });
};

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App , alertNess},
  template: '<App/>',

})



