<template>
  <div>
    <transition-group tag="div" name="slide"  :style="cssPropsSlider">
      <div v-for="number in [currentImg]" v-bind:key="number"  >
        <img :src="imgList[Math.abs(currentImg) % imgList.length]" :style="cssPropsImg"  v-on:click="handleClick(imgList[number])" />
      </div>
    </transition-group>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "ImageSliderButtonClickComponent",
  data () {
    return {
      show: true,
      currentImg: 0,
      iWidth: 400,
      iHeight: 400,
      dd:100
    }
  },
  created() {
    this.axios = axios.create({
      baseURL: this.$myUrl
    });
  },
  computed: {
    cssPropsSlider(){
      let dataX ;
      let dataY ;
      if(this.bCheckHoriz==false){
        dataX = 0;
        dataY =this.pTransData;
      }else{
        dataX = this.pTransData;
        dataY =0;
      }
      return  {
        overflow: "hidden",
        position: "relative",
        width: this.pWidthData+"px",
        height: this.pHeightData+"px",
        '--pHeight':dataY+"px",
        '--pWidth':dataX+"px",
        '--pSecond':this.pSecond+"s",

      }
    },
    cssPropsImg(){
      return {
        display: "flex",
        position: "absolute",
        top: 0,
        left: 0,
        bottom: 0,
        right:0,
        height: this.pHeightData+"px",
        width: this.pWidthData+"px",
      }
    },
  },
  props: {
    imgList:{
      Type:Array,
      default: ()=>[
        'https://www.shutterstock.com/blog/wp-content/uploads/sites/5/2019/09/shutterstock_1151676383.jpg?w=2000',
        'https://www.shutterstock.com/blog/wp-content/uploads/sites/5/2019/09/shutterstock_1151632343.jpg?w=2000',
        'https://www.shutterstock.com/blog/wp-content/uploads/sites/5/2019/09/shutterstock_1429964489.jpg?w=2000']
    },

    pWidthData: {
      type: Number,
      default: 400
    },
    pHeightData: {
      type: Number,
      default: 400
    },
    pTransData: {
      type: Number,
      default: 0
    },
    bCheckHoriz:{
      type:Boolean,
      default:false
    },
    pSecond:{
      type:Number,
      default:1
    },

  },
  mounted() {
    setInterval(() => {
      this.currentImg = this.currentImg + 1;
    }, 3000);
  },
  methods:{
    getTransform() {
      //build string here
      let str = "translate3d(0 + 'px', 400 + 'px', 0 + 'px')";
      return str;
    },
    handleClick(imgUrl) {
      // code to handle the click event
      try {
        // 데이터 가져오기

        this.axios.get('/Product', {
          params: {
            imageUrl: imgUrl,
          }
        })
          .then((response) => {
            this.$router.push({ name: 'Product', params: { imageUrl: imgUrl }}).then(() => {

            });
          })
          .catch((error) => {
          });
      } catch(error) {
        // 예외 처리
      }

    },
  }
}
</script>

<style scoped>
:root{
  --pHeight:var;
  --pWidth:var;
  --pSecond:var;
}
#demo {
  overflow: hidden;
}

.slide-leave-active,
.slide-enter-active {

  transition: all var(--pSecond);
  /*

  --height:v-bind(${this.pHeightData}px);*/
}

.slide-enter {
  transform: translate3d(var(--pWidth),var(--pHeight),0);
}

.slide-leave-to {
  transform: translate3d(calc(var(--pWidth)*-1),calc(var(--pHeight)*-1),0);
}

</style>



