import Vue from 'vue'
import Router from 'vue-router'
import axios from "axios";
import HelloWorld from '@/components/HelloWorld'

import TheWelcome from '@/components/TheWelcome'
import TesterLogin from "../components/Test/TesterLogin";
import SignInComponent from "../components/commonComponents/SignInComponent";
import SignUpComponent from "../components/commonComponents/SignUpComponent";
import Main from '../components/Main'
import Product from "../components/Product/Product";
import ProductComponent from "../components/Product/ProductComponent";
import Admin from "../components/Admin/Admin";
import PostForm from "../components/PostBoard/PostForm";
import PostList from "../components/PostBoard/PostList";
import PostDetail from "../components/PostBoard/PostDetail";
import PostModify from "../components/PostBoard/PostModify";
import AdminProduct from "../components/Admin/Product/AdminProduct";
import AdminProductRegist from "../components/Admin/Product/AdminProductRegist";
import AdminPost from "../components/Admin/Post/AdminPost";
import AdminPostDetail from "../components/Admin/Post/AdminPostDetail";
import AdminProductDetail from "../components/Admin/Product/AdminProductDetail";
import failComponent from "../components/commonComponents/FailComponent";
import Intro from "../components/Intro/Intro"
import AlertNess from "../components/commonComponents/AlertNess";
import Basket from "../components/Basket/Basket";
Vue.use(Router)

export default new Router({
  mode: 'history',

  routes: [
    {
      path: '/Main',
      name: 'Main',
      component: Main
    },
    {
      path: '/SignUp',
      name: 'SignUp',
      component: SignUpComponent
    },
    {
      path: '/SignIn',
      name: 'SignIn',
      component: SignInComponent
    },
    {
      path: '/',
      name: 'TesterLogin',
      component: TesterLogin
    },
    {
      path: '/users',
      name: 'TesterLogin',
      component: TesterLogin
    },
    {
      path: '/Products',
      name: 'Products',
      component: Product,
      props: true
    },
    {
      path: '/Product',
      name: 'Product',
      component: ProductComponent,
      props: true
    },
    {
      path: '/Admin',
      name: 'Admin',
      component: Admin,
      props: true
    },
    {
      path: '/PostForm',
      name: 'PostForm',
      component: PostForm,
      props: true
    },
    {
      path: '/PostList',
      name: 'PostList',
      component: PostList,
      props: true
    },
    {
      path: '/PostDetail',
      name: 'PostDetail',
      component: PostDetail,
      props: true
    },
    {
      path: '/PostModify/:id',
      name: 'PostModify',
      component: PostModify,
      props: true
    },
    {
      path: '/Admin/Product',
      name: 'AdminProduct',
      component: AdminProduct,
      props: true
    },
    {
      path: '/Admin/ProductRegist',
      name: 'AdminProductRegist',
      component: AdminProductRegist,
      props: true
    },
    {
      path: '/Admin/Post',
      name: 'AdminPost',
      component: AdminPost,
      props: true
    },
    {
      path: '/Admin/PostDetail/:id',
      name: 'AdminPostDetail',
      component: AdminPostDetail,
      props: true
    },
    {
      path: '/Admin/AdminProductDetail/:id',
      name: 'AdminProductDetail',
      component: AdminProductDetail,
      props: true
    },
    {
      path: '/fail',
      name: 'FailComponent',
      component: failComponent,
    },

    {
      path: '/Intro',
      name: 'intro',
      component: Intro,
      props: true
    },
    {
      path: '/Basket',
      name: 'Basket',
      component: Basket,
      props: true
    },

  ]
});




